$white: #e5e5e5;
$main-color: #8c6049;
$main-black: #101010;
$debug: 1px solid red;
$padding-mobile: 1.5rem 1.2rem;
$font: "Raleway", sans-serif;

body {
  background-color: #000;
}

.banner {
  max-width: 100%;
  position: absolute;
  z-index: -1;
}

.home {
  // border: $debug;
}

.banner-wrapper {
  background: url("../../assets/mobile-banner.jpg") no-repeat center
    center/cover;
  // background-attachment: fixed;
}

.navbar {
  // border: 1px solid red;
  padding: $padding-mobile;
  display: flex;
  justify-content: space-between;

  .logo {
    // border: 1px red solid;
    width: 155px;
  }

  .nav-items {
    // border: 1px red solid;
    display: flex;

    .wishlist-icon {
      color: $white;
      width: 23px;
      height: 23px;
      fill: transparent;
      transition: fill 0.1s ease-in-out;
      cursor: pointer;

      &:hover {
        fill: $white;
      }
    }

    .cart-icon {
      color: #d3d3d3;
      width: 23px;
      height: 23px;
      fill: transparent;
      transition: fill 0.1s ease-in-out;
      cursor: pointer;
      margin-left: 35px;

      &:hover {
        fill: $white;
      }
    }
  }
}

.hero-container {
  padding: $padding-mobile;
  // border: $debug;
  // margin-bottom: 90px;
  // background: url("../../assets/mobile-banner.jpg") no-repeat center
  //   center/cover;

  .hero-heading {
    color: $white;
    font-family: $font;
    font-weight: 300;
    font-size: 35px;
    margin-top: 20px;
    line-height: 125%;
  }

  .hero-sub-heading {
    // border: 1px red solid;
    color: #9b9b9b;
    font-size: 14px;
    line-height: 148%;
    letter-spacing: 1px;
    font-family: $font;
    font-weight: 400;
    margin-top: 30px;
  }

  .cta {
    max-width: 368px;
    width: 100%;
    height: 48px;
    border: 1px $main-color solid;
    background: rgba(16, 16, 16, 0.4);
    margin-top: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 25px 0 20px;
    cursor: pointer;
    margin-bottom: 80px;

    .btn-text {
      color: $main-color;
      font-family: $font;
      font-weight: 500;
      font-size: 16px;
      letter-spacing: 1.3px;
      // border: 1px red solid;
    }

    .right-arrow {
      // border: 1px red solid;
      color: $main-color;
      width: 30px;
      height: 30px;
    }
  }
}

// categories component

.categories-container {
  padding: $padding-mobile;
  background: $main-black;
  // border: $debug;

  .categories-heading {
    color: #7b7b7b;
    font-family: $font;
    font-weight: 500;
    letter-spacing: 1.2px;
    font-size: 11px;
    text-transform: uppercase;
    margin-bottom: 25px;
  }

  .category-slider {
    display: flex;
    // border: $debug;

    img {
      max-width: 100%;
    }

    .category-box {
      display: flex;
      flex-direction: column;
      align-items: center;
      // border: $debug;
      margin-right: 30px;

      .category-box-label {
        font-family: $font;
        color: $white;
        // font-weight: 400;
        font-size: 12px;
        padding-top: 15px;
      }
    }

    .category-box-last {
      margin-right: 0;
    }
  }
}
